import React, {Component} from 'react'
import {HashRouter, Link, Route, Redirect} from 'react-router-dom'
import '../styles/main.css';
import axiosApi from '../axiosApi'
import Xtorage from './Xtorage'


class Projects extends Component{
constructor(props){
    super(props)
    this.state={
          checked: false,
          date:'',
          redirect: false,
          status:''
        }

}

CheckedDates=(dateDay,dateHours)=>{
    this.setState({
                dateDay: dateDay,
                dateHours:dateHours,
                checked:true
            })
}


collaborate=() => {
      const userId = Xtorage.local.get('userId')
  //el xtorage.local.set el faig en el Index.js de React. Aqu recupero el id del usuari

     var  collaboration = {
      festival: this.props.festival,
      projectId: this.props.project._id,
      dateDay:this.state.dateDay,
      dateHours:this.state.dateHours
     }
      Xtorage.local.setObject('collaboration', collaboration) //('key',value) es el format del localstorage
     console.log('hola jalbert')


    axiosApi.collaborate(userId,collaboration.festival._id,collaboration.projectId,collaboration.dateDay,collaboration.dateHours)
    .then((response) =>{response.data.status ==='KO' ?  this.setState({status:'KO'}) : this.setState({status:'OK'}) /*this.setState({redirect: true})*/})
    .catch(console.error)
}

tryCollaboration = () => {
   this.setState({status: undefined})// me limpia el estado de la llamada axios anterior, la llamo desde el boton Apuntame
}


	render(){
    console.log(this.props.project.available_dates,'hola')
    if(this.state.redirect) return <Redirect to='/register/' />
    else return (
       <section className="festprojects">
              <div className="ong ong1"><h3>{this.props.project.name}</h3><br />
              <span>{this.props.project.description}</span>
              <span>{this.props.project.location}</span>
              <div className="container">
              <ul className="list">
                {this.props.project.available_dates.map((date)=>
                  <li className="list__item">
                    <input type="radio" className="radio-btn" name="choice" id={date._id} 
                    value={date.day} onChange={()=> this.CheckedDates(date.day,date.hours)}/>
                    <label for="a-opt" className="label"> Das disponibles: {date.day}<br/> Horas: {date.hours} Hrs</label>
                 </li>)}
              </ul>
              </div>
              <button className="btn  btn-xs but" type="button" 
              data-toggle="modal" data-target={"#confirmInfo-" + this.props.project._id} onClick={this.tryCollaboration}> PARTICIPAR AQUI</button>
                  <div id={"confirmInfo-" + this.props.project._id} className="modal fade" role="dialog">
                                <div className="modal-dialog">
                                  <div className="modal-content">
                                    <div className="modal-header">
                                      <h4 className="modal-title  text-center">EVENTO: {this.props.festival.name}</h4>
                                    </div>
                                    <div className="modal-body">
                                      <div>
                                        <h4 className="modal-title  text-center">ONG SELECCIONADA: {this.props.project.name}</h4>
                                        <span>FECHA ESCOGIDA: {this.state.dateDay}</span><br/>
                                        <span>HORAS DE VOLUNTARIADO:{this.state.dateHours}</span>
                                      </div>
                                      <div className="row">
                                          <div className="col-12-xs text-center">
                                            {!this.state.status? <button className="btn btn-success btn-md but" 
                                              onClick={this.collaborate}>SI, SEGUIR</button> : this.state.status ==='KO' ? <div>Solo se puede participar una vez por cada  Festival, gracias</div> : <button>Registrate para seguir</button>}
                                              <button className={!this.state.status? 'btn btn-danger btn-md but' : this.state.status ==='KO' ? 'btn btn-danger btn-md but' : 'btn btn-success btn-md but'} data-dismiss="modal">{!this.state.status? 'NO, VOLVER ATRS' : 'Volver al Inicio'}</button>
                                          </div>
                                      </div>
                                    </div>
                                  </div>
                                </div>
                              </div>

              </div>
      </section>

)
	}

}

export default Projects




