import axios from 'axios'


const  axiosApi = {
	baseUrl :'https://obscure-stream-32522.herokuapp.com',

	retrieveFestivalByName : function (festivalName) {
		return axios.get(this.baseUrl +'/festivals/name/' + festivalName)
		.then((data)=>{
			return data.data // pq axios ja posa lobjecte dins d'un data
		})
		.catch( (err) => {
			console.error(err)
		})

	},



	collaborate : function (userId, festivalId, projectId, dateDay, dateHours) {
		return axios.post(this.baseUrl + '/festival/' + festivalId + '/project/' + projectId + '/collaborate',
		{userId, dateDay, dateHours}) //{esto es lo que se le enva por el body} Aqu se use la info del local storage entre ella y con la BD
		.then((localdata)=>{
			return localdata // el post igualmnte siempre devuelve una respuesta
		})
		.catch((err)=>{
			console.error(err)
		})
	}

}



export default axiosApi


